body {
  background-color: red;
  background-color: #E9F3F8;
}


/*
 * Filter page
 */
.filter_container {
  display: grid;
  grid-template-columns: 20% auto;
  grid-template-rows: 400px;
  grid-column-gap: 5%;

  background: white;
  padding: 50px;
  width: 1000px;
  margin: 0 auto;
}
.form_container{
  background: #E9F3F8;
}
.table_container{
  background: #E9F3F8;
  position: relative;
}
.option_cell{
  position: relative;
  display: block;
  padding: 15px;
  border-bottom: 1px solid black;
}
.option_cell input {
  position: absolute;
  right: 15px;
}
.filter_button button{
  width: 100%;
  height: 100%;
  background-color: #003469;
  border: none;
  color: white;
  padding: 20px;
}
.export_buttons input{
  width: 25%;
  height: 25%;
  border: none;
}
.gegTable{
  width: 100%;
  table-layout: fixed;
}
.gegTable,.gegTable th,.gegTable td {
  border: 1px solid darkgray;
  border-collapse: collapse;
}
.gegTable th{
  text-align: center;
  background-color: #e00049;
  color: #E9F3F8;
}
.gegTable th{
  height: 50px;
}
.gegTable td{
  padding: 15px;
  text-align: right;
}
.pagination_container{
  line-height: 20px;
  position: absolute;
  bottom:0;
  /* move the element half way across the screen */
  left: 50%;
  /* allow the width to be calculated dynamically */
  width: auto;
  /* then move the element back again using a transform */
  transform: translateX(-50%);
}
.pagination_container ul{
  list-style-type: none;
  display: inline-block;
  margin: auto;
  padding-inline-start: 0px;
}
.pagination_container li {
  float: left;
  padding-left: 5px;
  padding-right: 5px;
}